	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wedding in the Mountains","date":"20 Aug 2002","slug":"/2002-08-20-wedding-in-the-mountains/","tags":"Y2002"},"html":"<p>Congratuations to <a href=\"http://andrea.editthispage.com/2002/08/20\">Andrea</a> and <a href=\"http://spicynoodles.org/2002/08/19\">André</a>,  who have been <a href=\"http://hal.editthispage.com/2002/08/19\">doing</a> more in <a href=\"http://www.dangerousmeta.com/posts/02/20020819\">New Mexico</a> than just viewing the scenery. It looks like a much nicer looking place for a wedding than the typical German Standesamt.</p>"},"next":{"fields":{"title":"Americans Stay Home","date":"20 Aug 2002","slug":"/2002-08-20-americans-stay-home/"}},"previous":{"fields":{"title":"Faster But Lower","date":"21 Aug 2002","slug":"/2002-08-21-faster-but-lower/"}}},{"node":{"fields":{"title":"Americans Stay Home","date":"20 Aug 2002","slug":"/2002-08-20-americans-stay-home/","tags":"Y2002"},"html":"<p>NY Times <a href=\"http://www.nytimes.com/2002/08/18/international/europe/18EURO.html\">Americans Stay Home, and Europe Feels a Pinch</a>. My folks noticed that their flight to Frankfurt was just 60% full,  and the tour dates to Scandinavia they wanted were 'unavailable' (they suspect cancelled). They are joining their tour tomorrow, and are hoping things will be not crowded. Their return flight to the States is on Friday the 13th. That might be mostly empty. :-)</p>"},"next":{"fields":{"title":"Flood Water Coming","date":"19 Aug 2002","slug":"/2002-08-19-flood-water-coming/"}},"previous":{"fields":{"title":"Wedding in the Mountains","date":"20 Aug 2002","slug":"/2002-08-20-wedding-in-the-mountains/"}}},{"node":{"fields":{"title":"Flood Water Coming","date":"19 Aug 2002","slug":"/2002-08-19-flood-water-coming/","tags":"Y2002"},"html":"<p>The flood waters from Dresden are heading north. Flooding is expected in the outskirts upstream of Hamburg by Thursday or Friday. However, Hamburg itself will be barely affected, since the harbor is large enough to disapate the flood. Our county (Landkreis Harburg) has declared an emergency for the Elbmarsch, where 12000 residents may have to be evacuated. The PapaScott residence is 50 km away from the area that might be flooded, so we are in no danger.</p>"},"next":{"fields":{"title":"Vacation Weekend","date":"18 Aug 2002","slug":"/2002-08-18-vacation-weekend/"}},"previous":{"fields":{"title":"Americans Stay Home","date":"20 Aug 2002","slug":"/2002-08-20-americans-stay-home/"}}}],"pathPrefix":"","first":false,"last":false,"index":857,"pageCount":1101}}

